import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl,Validators,FormBuilder } from '@angular/forms';
import { CustomerSevice } from '../services/customer.service';
import { Customer } from '../model/customer';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent{
  btnText = "Add Customer";
  allUsers = [];
  profileForm :FormGroup;
constructor(private fb: FormBuilder , private customerService : CustomerSevice){}

  ngOnInit() {
    this.profileForm=this.fb.group({
  
      firstName: ['' , Validators.required],
      lastName: ['', Validators.required],
      email: ['',[Validators.required,Validators.email]],
      password: ['', [Validators.required,Validators.minLength(8)]]
  });
}
  getData(){
    this.customerService.getAllCustomer().subscribe(data => {
      this.allUsers = data;
    });
  }
    // saveCustomer(profileForm){
    //    console.log(profileForm);
    //   }
  saveCustomer(user: Customer){
    if(this.btnText === "Add Customer"){

      this.customerService.addUser(user).subscribe(data => {
        this.profileForm.reset();
        this.getData();
       
       })
     }else{
      //  this.customerService.updateUserById(this.selectedUserId,user).subscribe(data => {
      //   //  this.adminForm.reset();
      //    this.getData();
      //   //  this.cancel();

     
       }
     }
   }
  